.home {
    font-family:$fontM;
}
.content {
    > .inner {
        width:1180px;
        margin:225px auto 0;
        position:relative;
        opacity:0;
        transform:translateY(80px);
        transition:opacity .5s, transform 1s;
        
        h2 {
            position:relative;
            font-weight:300;font-size:80px;line-height:1;margin-bottom:10px;
        }
        .desc_sub {
            position:relative;
            margin:15px 0 0 10px;
            padding:0 0 50px 20px;
            font-weight:300;font-size:20px;line-height:30px;
        
            &::after {
                content:"";
                position:absolute;left:-14px;top:0;
                width:30px;height:30px;
                background:url(../../../public/img/ico_star.svg) no-repeat 0 0;background-size:30px auto;
            }
            
        }
    }
    &.on {
        figure {
            opacity:1;
        }
        >.inner {
            opacity:1;
            transform:translateY(0);
        }
    }

    .info {
        min-height:600px;
        padding-top:100px;
        border-top:1px solid #000;
    }
}


@media screen and (max-width:$tablet) {
    .content {
        > .inner {
            width:100%;
            padding:0 25px;
            margin-top:150px;
            h2 {
                font-size:60px;
            }
            
        }
        .info {
            padding-top:60px;
        }
    }
}

@media screen and (max-width:$mobile) {
    .content {
        > .inner {
            h2 {
                font-size:50px;
            }
            .desc_sub {
                font-size:18px;
                line-height:26px;
                padding:0 0 50px 15px;
                &::after {
                    top:-3px;
                    transform:scale(.7);
                }
             }
        }
    }
}